* {
  box-sizing: border-box;
}

.page {
  min-height: 100vh;
}

.page .content-area {
  width: calc(100% - 250px);
  min-height: 100%;
  transition: .3s;
  will-change: width;
  float: right;
}

.page .sidebar {
  width: 250px;
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  background-color: #333;
  padding: 15px;
  transition: .3s;
}
